import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useForm, SubmitHandler } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from 'yup';
import { useTranslation } from 'react-i18next';

interface RegisterFormData {
  person: 'private' | 'organization';
  name: string;
  username: string;
  email: string;
  password: string;
  passwordAgain: string;
  remember?: boolean;
}

const RegisterForm: React.FC = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();

  const schema = yup.object().shape({
    name: yup.string().required(t('register.name') + ' ' + t('validation.emailRequired')),
    email: yup.string().email(t('validation.emailInvalid')).required(t('validation.emailRequired')),
    username: yup.string().required(t('register.username')),
    password: yup.string().min(6, t('validation.passwordMin', { min: 6 })).max(20).required(t('validation.passwordRequired')),
    passwordAgain: yup.string().oneOf([yup.ref('password'), null], t('register.passwordAgain')).required(t('register.passwordAgain')),
  });

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm<RegisterFormData>({
    resolver: yupResolver(schema),
  });

  const onSubmit: SubmitHandler<RegisterFormData> = (data) => {
    console.log('register submit', data);
    // after successful register navigate to login
    navigate('/logiranje');
  };

  return (
    <main>
      <h2>{t('register.register') || t('nav.register')}</h2>

      <form id="register-form" onSubmit={handleSubmit(onSubmit)}>
        <label htmlFor="person">{t('register.person')}</label>
        <select {...register('person')} name="person" id="person">
          <option value="private">{t('registerOptions.private')}</option>
          <option value="organization">{t('registerOptions.organization')}</option>
        </select>

        <label htmlFor="name">{t('register.name')}</label>
        <input type="text" id="name" placeholder={t('register.name')} {...register('name')} />
        <p className="error">{errors.name?.message as string}</p>

        <label htmlFor="username">{t('register.username')}</label>
        <input type="text" id="username" placeholder={t('register.username')} {...register('username')} />
        <p className="error">{errors.username?.message as string}</p>

        <label htmlFor="email">{t('register.email')}</label>
        <input type="email" id="email" placeholder={t('register.email')} {...register('email')} />
        <p className="error">{errors.email?.message as string}</p>

        <label htmlFor="password">{t('register.password')}</label>
        <input type="password" id="password" placeholder={t('register.password')} {...register('password')} />
        <p className="error">{errors.password?.message as string}</p>

        <label htmlFor="password-again">{t('register.passwordAgain')}</label>
        <input type="password" id="passwordAgain" placeholder={t('register.passwordAgain')} {...register('passwordAgain')} />
        <p className="error">{errors.passwordAgain?.message as string}</p>

        <button type="submit" id="register" className="details">
          {t('button.register')}
        </button>
      </form>
      <p>
        {t('register.remember')} <input type="checkbox" {...register('remember')} />
      </p>
      <p>
        <strong>{t('register.already')}</strong>
      </p>
      <p className="log" onClick={() => { navigate('/logiranje'); }}>
        {t('nav.login')}...
      </p>

      <div className="alternative-register">
        <h3>{t('register.altRegister')}</h3>
        <span className="eky-icon eky-google card">{t('social.google')}</span>
        <span className="eky-icon eky-facebook card">{t('social.facebook')}</span>
      </div>
    </main>
  );
};

export default RegisterForm;
