import React from 'react';
import { useForm, SubmitHandler } from 'react-hook-form';
import { useTranslation } from 'react-i18next';

interface AddDogFormData {
  name: string;
  breed: string;
  color?: string;
  location: string;
  age: number;
  description: string;
  size: 'small' | 'medium' | 'large';
  vaccinated?: boolean;
  neutered?: boolean;
}

const AdddogForm: React.FC = () => {
  const { t } = useTranslation();
  const { register, handleSubmit } = useForm<AddDogFormData>();

  const onSubmit: SubmitHandler<AddDogFormData> = (data) => {
    console.log('add dog', data);
  };

  return (
    <main>
      <h2>{t('adddog.title')}</h2>
      <form id="adddog-form" onSubmit={handleSubmit(onSubmit)}>
        <label htmlFor="dogname">{t('adddog.name')}</label>
        <input type="text" id="dogname" placeholder={t('adddog.name')} {...register('name')} />

        <label htmlFor="dogbreed">{t('adddog.breed')}</label>
        <input type="text" id="dogbreed" placeholder={t('adddog.breed')} {...register('breed', { required: true })} />

        <label htmlFor="dogcolor">{t('adddog.color')}</label>
        <input type="text" id="dogcolor" placeholder={t('adddog.color')} {...register('color')} />

        <label htmlFor="doglocation">{t('adddog.location')}</label>
        <input type="text" id="doglocation" placeholder={t('adddog.location')} {...register('location', { required: true })} />

        <label htmlFor="dogage">{t('adddog.age')}</label>
        <small>{t('adddogExtra.ageNote')}</small>
        <input type="number" id="dogage" placeholder={t('adddog.age')} step="0.1" {...register('age', { valueAsNumber: true })} />

        <label htmlFor="description">{t('adddog.description')}</label>
        <textarea rows={3} cols={30} placeholder={t('adddog.description')} {...register('description')} />

        <label htmlFor="dogsize">{t('adddog.size')}</label>
        <select {...register('size')} id="dogsize">
          <option value="small">{t('size.small')}</option>
          <option value="medium">{t('size.medium')}</option>
          <option value="large">{t('size.large')}</option>
        </select>

        <div id="health">
          <label htmlFor="vacine">
            <span>{t('adddog.vaccinated')}</span>
            <input type="checkbox" {...register('vaccinated')} id="vacine" />
          </label>

          <label htmlFor="castrat">
            <span>{t('adddog.neutered')}</span>
            <input type="checkbox" {...register('neutered')} id="castrats" />
          </label>
        </div>
        <button type="submit">{t('button.addDog')}</button>
      </form>
    </main>
  );
};

export default AdddogForm;
