import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

interface CardSmallProps {
  imgSrc: string;
  name: string;
  gender?: string;
  age?: number;
  city?: string;
  likes?: number;
  breed?: string;
}

const CardSmall: React.FC<CardSmallProps> = ({ imgSrc, name, gender = '', age, city, likes, breed }) => {
  const navigate = useNavigate();
  const { t } = useTranslation();
  return (
    <>
      <div className="card">
        <div className="img">
          <img src={imgSrc} alt={name} />
        </div>

        <div className="description">
          <h3>
            <strong>{name}</strong>, <span className={gender}></span>
            {age ? ` ${age} ${t('dogDetails.ageSuffix')} - ` : ' - '}
            {breed}
          </h3>
          <p className="meta">
            <span className="eky-icon eky-map"></span>
            {city}
          </p>
          <p className="meta">
            <span className="eky-icon eky-heart"></span>
            {likes}
          </p>

          <button className="details" onClick={() => { navigate('/DogDetails'); }}>
            {t('button.viewDetails')}
          </button>
        </div>
      </div>
    </>
  );
};

export default CardSmall;
