import React from 'react';
import { Routes, Route, Link } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import LanguageSelector from './LanguageSelector';

import About from './pages/About';
import LoginForm from './pages/LoginForm';
import RegisterForm from './pages/RegisterForm';
import AdddogForm from './pages/AdddogForm';

import DogList from './pages/DogList';

const Navbar: React.FC = () => {
  const isLoged = true;
  const { t } = useTranslation();

  return (
    <>
      <nav id="top">
        <ul className="top-nav">
          <Link to="/">{t('nav.about')}</Link>
          <Link to="logiranje">{t('nav.login')}</Link>
          <Link to="registracija">{t('nav.register')}</Link>
          {isLoged ? <Link to="psi">{t('nav.dogs')}</Link> : ''}
          <li style={{ marginLeft: '1rem' }}>
            <LanguageSelector />
          </li>
        </ul>
      </nav>
      <Routes>
        <Route path="/" element={<About />} />
        <Route path="/logiranje" element={<LoginForm />} />
        <Route path="/registracija" element={<RegisterForm />} />
        <Route path="/psi/*" element={<DogList />} />
        <Route path="/dodajpsa" element={<AdddogForm />} />
      </Routes>
    </>
  );
};

export default Navbar;
